<script setup>
  import Technical from './Technical.vue';
  import Card from './Card.vue';
  import UiButton from '@/components/UI/UiButton';
  // import CompareTable from '@/components/common/CompareTable/index.vue';

  import { ref, watchEffect } from 'vue';
  import jsonTariff from './tariffs.json';
  const level = ref('basic');
  const model = ref('origin');
  const bandWidth = ref('50');
  const protectionLevel = (event) => {
    level.value = event;
  };
  const protectionModel = (event) => {
    model.value = event;
  };
  const BandWidth = (event) => {
    bandWidth.value = event;
  };
  const BandWidthUpdater = (event) => {
    bandWidth.value = event;
    console.log('BandWidthUpdater', event);
  };
  // console.log(xyeta());
  const jsonData = ref(jsonTariff);
  const findPrice = (model, bandWidth, level, currency) => {
    return jsonData.value[model][bandWidth][level][currency].price;
  };
  const currentPrice = ref(null);
  const valuta = ref('USD');

  // const protectionLevel = (event) => {
  //   level.value = event;
  //   findPrice();
  // };
  // const protectionModel = (event) => {
  //   model.value = event;
  //   findPrice();
  // };
  // const BandWidth = (event) => {
  //   bandWidth.value = event;
  //   findPrice();
  // };
  // const BandWidthUpdater = (event) => {
  //   bandWidth.value = event;
  //   findPrice();
  // };
  // const jsonData = ref(jsonTariff);
  // const findPrice = () => {
  //   currentPrice.value =
  //     jsonData.value[model.value][bandWidth.value][level.value][
  //       valuta.value
  //     ].price;
  // };

  watchEffect(() => {
    bandWidth.value;
    model.value;
    level.value;
    setTimeout(() => {
      currentPrice.value = findPrice(
        model.value,
        bandWidth.value,
        level.value,
        valuta.value,
      );
    });
  });

  //modal
  import CompareTable from '@/components/common/CompareTable/index.vue';
  import { useModalSlot } from 'vue-final-modal';
  import { createModal } from '@/services/createModal';
  const compateTableModal = createModal(
    CompareTable,
    'Protection Levels Comparison',
    '',
    'modal-container--fullScreen',
  );
  compateTableModal.patchOptions({
    slots: {
      default: useModalSlot({
        attrs: {
          onConfirm() {
            compateTableModal.close();
          },
        },
      }),
    },
  });
</script>

