      padding: 0 50px;
    }
    @include media-sm-min {
      margin-top: 60px;
    }
  }
  .ui-slider {
    border-radius: 30px;
    max-width: 100%;
    @include media-xs-min {
      max-width: 610px;
    }
    @include media-sm-min {
      max-width: 920px;
    }
  }
  //slider navigation
  .slider-nav {
    &--white {
      .arrow {
        filter: brightness(0) invert(1);
      }
    }
    .arrow {
      width: 45px;
      height: 45px;
      position: absolute;
      fill: $white;
      z-index: 10;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
      bottom: -60px;
      @include media-xs-min {
        bottom: auto;
        top: 50%;
        transform: translateY(-50%);
      }
      @include media-sm-min {
        border: 1px solid $blue-main;
      }
      svg {
        fill: $blue-main;
        width: 15px;
        transition: linear 0.2s;
        height: 15px;
      }
      &--disabled {
        pointer-events: none;
        border-color: $mono-lables;
        opacity: 0.5;
        img {
          filter: grayscale(1);
        }
        svg {
          path {
            fill: $mono-lables;
          }
        }
      }
      &--left {
        left: 0px;
      }
      &--right {
        left: auto;
        right: 0px;
      }
    }
  }
  .slider__pagination {
    display: flex;
    justify-content: center;
    margin-top: 33px;
    @include media-xs-min {
      margin-top: 40px;
    }
    &--white {
      .slider__pagination-dot {
        background: $white;
        opacity: 0.4;
        &--is-active {
          opacity: 1;
          background: $white;
        }
      }
    }
    &-dot {
      width: 8px;
      height: 8px;
      border: none;
      border-radius: 50%;
      background: $blue-focus;
      &:not(:last-child) {
        margin-right: 15px;
      }
      &--is-active {
        background: $blue-main;
      }
    }
  }

  .fader {
    height: 50vw;
    position: relative;
    overflow: hidden;
  }

  @media (min-width: 768px) {
    .fader {
      height: 300px;
    }
  }

  .fader__slide {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .fader img {
    width: 100%;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    -webkit-transform: translateY(-50%) translateX(-50%);
  }
</style>
