<template>
  <div class="ui-radio-tabs">
    <div class="ui-radio-tabs-wrapper">
      <div
        v-for="(item, index) in radioObject"
        :key="index"
        :class="[
          'ui-radio-tabs__card',
          { 'ui-radio-tabs__card--is-active': radioValue === item.level },
        ]"
      >
        <div
          :class="[
            'ui-radio-tabs__card-additional',
            item.color ? `ui-radio-tabs__card-additional--${item.color}` : '',
          ]"
        >
          {{ item.aditional }}
        </div>
        <div class="ui-radio-tabs__card-head">
          <label class="ui-radio-tabs__card-head-label" :for="item.level">
            <input
              class="ui-radio-tabs__card-head-input"
              type="radio"
              :name="radioValue"
              @change="({ target }) => (radioValue = target.value)"
              v-model="radioValue"
              :value="item.value"
              :id="item.level"
            />
            <span>{{ item.level }}</span>
          </label>
        </div>
        <div
          v-if="radioValue === item.value"
          class="ui-radio-tabs__card-content"
        >
          <p
            v-for="(list, index) in item.list"
            :key="index"
            class="ui-radio-tabs__card-content-details"
          >
            {{ list }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
  import { ref, watchEffect, defineEmits } from 'vue';

  const props = defineProps({
    modelValue: {
      type: String,
      default: 'basic',
    },
    radioObject: {
      type: Object,
      requred: true,
    },
  });

  const emit = defineEmits('update:modelValie');
  const radioValue = ref(props.modelValue);
  watchEffect(() => {
    emit('update:modelValue', radioValue.value);
  });
</script>

