<!-- eslint-disable max-len -->
<template>
  <div class="ui-accordion">
    <div
      v-for="(text, index) in faq"
      :key="index"
      :class="[
        'ui-accordion__card',
        { 'ui-accordion__card--is-active': addActiveFaq(index) },
      ]"
      @click="faqTrigger(index)"
    >
      <div class="ui-accordion__card-head">
        <p class="ui-accordion__card-title title" v-html="$t(text.title)" />
        <svg width="9" height="17" viewBox="0 0 9 17" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:class="'ui-accordion__card-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M8.121 16.414a.405.405 0 01-.598 0L.141 9.066a.476.476 0 010-.597L7.523 1.12a.405.405 0 01.598 0l.703.668c.14.176.14.457 0 .598L2.461 8.75l6.363 6.398c.14.141.14.422 0 .598l-.703.668z" fill="#07F"/></svg>
      </div>
      <div
        :class="[
          'ui-accordion__card-content',
          { 'ui-accordion__card-content--no-padding': !text.details },
        ]"
      >
        <p v-if="text.details" v-html="$t(text.details)" />
        <div
          v-for="(item, index) in text.colums"
          :key="index"
          class="ui-accordion__card-content-block"
        >
          <div class="column">
            <span>{{ item.columsName }}</span>
            <UiToolTip
              v-if="item.columnsToolType"
              :content="item.columnsToolType"
            />
          </div>
          <div class="column">
            <img
              v-if="item.basicImage"
              svg-inline
              :src="require(`@/assets/images/${item.basicImage}`)"
              alt=""
            />
            <p v-if="item.basicText" v-html="item.basicText"></p>
          </div>
          <div class="column">
            <img
              v-if="item.optimalImage"
              svg-inline
              :src="require(`@/assets/images/${item.optimalImage}`)"
              alt=""
            />
            <p v-if="item.optimalText" v-html="item.optimalText"></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
  import { ref } from 'vue';
  import UiToolTip from './UiTooltip.vue';
  defineProps({
    faq: {
      type: Object,
      required: true,
    },
  });
  const isCurrentFaq = ref(false);
  const faqTrigger = (index) => {
    index === isCurrentFaq.value
      ? (isCurrentFaq.value = false)
      : (isCurrentFaq.value = index);
  };
  const addActiveFaq = (index) => {
    return isCurrentFaq.value === index;
  };
</script>
