<template>
  <div class="ui-field">
    <label
      v-if="hasLabel"
      :class="{
        'ui-field__label': true,
      }"
    >
      <slot name="s-label" :label="label">
        {{ label }}
        <sup v-if="required" class="ui-field__label-sup">*</sup>
      </slot>
    </label>

    <component
      :is="tag"
      :id="id"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      :name="name"
      class="ui-field__input"
      :placeholder="placeholder"
      tabindex="0"
      :type="type"
    />
    <p class="ui-field__input-error">{{ $t(details) }}</p>
  </div>
</template>
<script setup>
  function hasLabel() {
    return !!this.$slots['s-label'] || !!props.label;
  }
  const props = defineProps({
    tag: {
      type: String,
      default: 'input',
    },
    modelValue: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    required: {
      type: Boolean,
      default: false,
    },
    details: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    name: {
      type: String,
      default: '',
    },
    id: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'text',
      validator(value) {
        return ['text', 'tel', 'email', 'password'].includes(value);
      },
    },
  });
</script>

<style lang="scss">
  .ui-field {
    display: flex;
    flex-direction: column;
    padding: 10px 0;
    position: relative;
    &--textarea {
      .ui-field__input {
        height: 60px;
      }
    }
    &.is-invalid {
      .ui-field__input {
        border-color: $red-main;
        background-color: $red-background;
        &-error {
          display: block;
        }
      }
    }
    &.is-valid {
      .ui-field__input {
        border-color: $green-main;
        background-color: none;
      }
    }
    & > * {
      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
    &__label {
      @include text(label, normal, normal);
      &-sup {
        font-size: 14px;
        top: -0.3em;
        color: $red-main;
      }
    }
    &__input {
      border: 1px solid $mono-border;
      padding: 5px 10px;
      outline: none;
      border-radius: 5px;
      &:focus {
        border-color: $blue-hover;
      }
      &-error {
        position: absolute;
        bottom: -8px;
        margin-top: 5px;
        display: none;
        color: $red-main;
        font-size: 12px;
        line-height: 20px;
      }
    }
  }
</style>
