<template>
  <div class="ui-range">
    <div class="ui-range-wrapper">
      <input
        v-model="selectedOutput"
        type="number"
        @input="onNumberInput"
        class="ui-range__input"
      />
      <input
        ref="slider"
        v-model="selectedValue"
        @input="({ target }) => (selectedValue = parseFloat(target.value))"
        type="range"
        min="0"
        :max="values.length - 1"
        class="ui-range__slider"
      />
    </div>
    <div class="ui-range__bottom">
      <span class="ui-range__bottom-number">{{ rangeMIn }}</span>
      <span class="ui-range__bottom-number">{{ rangeMax }}</span>
    </div>
  </div>
</template>

<script setup>
  import { ref, computed, onMounted, watchEffect, defineEmits } from 'vue';

  const props = defineProps({
    values: {
      type: Array,
      default: () => [50, 100, 200, 300, 400, 500, 600, 700, 800, 1000],
    },
  });
  const rangeMIn = computed(() => {
    return props.values[0];
  });
  const rangeMax = computed(() => {
    return props.values.at(-1);
  });
  const selectedValue = ref(0);
  const slider = ref();
  onMounted(() => {
    selectedValue.value = 0;
  });
  const selectedOutput = computed(() => {
    return props.values[selectedValue.value];
  });
  const onNumberInput = (event) => {
    const newValue = parseFloat(event.target.value);

    // Найти ближайшее значение из массива props.values
    const closestValue = findClosestValue(newValue);

    // Обновить selectedValue
    selectedValue.value = props.values.indexOf(closestValue);
  };

  // Функция для поиска ближайшего значения в массиве
  const findClosestValue = (targetValue) => {
    return props.values.reduce((prev, curr) => {
      return Math.abs(curr - targetValue) < Math.abs(prev - targetValue)
        ? curr
        : prev;
    });
  };
  const getProgress = (value, min, max) => {
    return ((value - min) / (max - min)) * 100;
  };
  // function to set the css variable for the progress
  const setCSSProgress = (progress) => {
    slider.value.style.setProperty('--ProgressPercent', `${progress}%`);
  };

  const emit = defineEmits(['bandWidth']);
  watchEffect(() => {
    if (slider.value) {
      // eslint-disable-next-line max-len
      // Обновить максимальное значение слайдера, иначе, если новый массив `values` длиннее, то это может вызвать проблемы
      slider.value.max = props.values.length - 1;

      // Пересчитать selectedValue, чтобы учесть новое максимальное значение слайдера
      selectedValue.value = Math.min(
        selectedValue.value,
        props.values.length - 1,
      );
      emit('bandWidth', selectedOutput);
      // update the slider progress
      const progress = getProgress(
        selectedValue.value,
        slider.value.min,
        slider.value.max,
      );
      // define extrawidth to ensure that the end of progress is always under the slider thumb.
      let extraWidth = (100 - progress) / 100;
      // set the css variable
      setCSSProgress(progress + extraWidth);
      if (!props.values.includes(selectedOutput.value)) {
        selectedValue.value = 0; // Например, вернуться к первому значению в массиве
      }
    }
  });
</script>
<style lang="scss">
  .ui-range {
    --trackHeight: 0.5rem;
    --thumbRadius: 15px;
    &-wrapper {
      position: relative;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      position: relative;
      padding-bottom: 10px;
    }
    &__input {
      padding: 4px 10px 6px 15px;
      border-radius: 5px;
      border: 1px solid $mono-border;
      font-size: 12px;
      line-height: 25px;
      outline: none;
      &:focus {
        border-color: $blue-focus;
      }
      &:active {
        border-color: $blue-focus;
      }
    }
    &__slider {
      position: absolute;
      bottom: 5px;
      width: 100%;
      appearance: none;
      border-radius: 999px;
      z-index: 0;
      &::before {
        content: '';
        position: absolute;
        width: var(--ProgressPercent, 100%);
        height: 100%;
        background: $blue-main;
        pointer-events: none;
        border-radius: 999px;
      }
      &::-webkit-slider-runnable-track {
        appearance: none;
        background: $blue-focus;
        height: var(--trackHeight);
        border-radius: 999px;
      }
      &::-moz-range-track {
        appearance: none;
        background: $blue-focus;
        height: var(--trackHeight);
        border-radius: 999px;
      }
      &::-webkit-slider-thumb {
        position: relative;
        width: var(--thumbRadius);
        height: var(--thumbRadius);
        margin-top: calc((var(--trackHeight) - var(--thumbRadius)) / 2);
        background: $blue-main;
        border: 2px solid $white;
        border-radius: 50%;
        pointer-events: all;
        appearance: none;
        z-index: 1;
      }
      &-numbers {
        display: flex;
        justify-content: space-between;
        margin-top: 5px;
        font-size: 11px;
      }
    }
    &__bottom {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: flex-start;
      &-number {
        @include text(paragraph, smaller, normal);
      }
    }
  }
</style>
