<template>
  <div class="ui-switcher">
    <div class="ui-switcher-wrapper">
      <input
        class="ui-switcher__input"
        type="radio"
        :name="switchValue"
        @change="({ target }) => (switchValue = target.value)"
        :value="valueLeft"
        v-model="switchValue"
        :id="valueLeft"
        checked
      />
      <label class="ui-switcher__label" :for="valueLeft"
        >{{ valueLeftName }}
        <UiTooltip
          content="<span>Best for customers looking for the benefits of regular corporate network protection. <br> <a target='_blank' href='https://ddos-guard.net/en/tutorials/network-protection/always-on-protection'>Learn more</a></span>"
      /></label>
      <input
        class="ui-switcher__input"
        type="radio"
        :name="switchValue"
        @change="({ target }) => (switchValue = target.value)"
        v-model="switchValue"
        :value="valueRight"
        :id="valueRight"
      />
      <label class="ui-switcher__label" :for="valueRight"
        >{{ valueRightName }}
        <UiTooltip
          content="<span>Best for customers looking to protect their network only during certain periods like sales or holidays. <a target='_blank' href='https://ddos-guard.net/en/tutorials/network-protection/on-demand-protection'>Learn more</a></span>"
      /></label>
    </div>
  </div>
</template>

<script setup>
  import { ref, watchEffect } from 'vue';
  import { UiTooltip } from '@/components/UI';
  const props = defineProps({
    valueLeft: {
      type: String,
      default: 'value 1',
    },
    valueLeftName: {
      type: String,
      default: 'First Title',
    },
    valueRight: {
      type: String,
      default: 'value 2',
    },
    valueRightName: {
      type: String,
      default: 'Second Name',
    },
    modelValue: {
      type: String,
      default: 'Значение1',
    },
  });
  const emit = defineEmits('update:modelValie');
  const switchValue = ref(props.modelValue);

  watchEffect(() => {
    emit('update:modelValue', switchValue.value);
  });
</script>

