    display: flex;
    color: $mono-main;
    flex-direction: column;
    &__block {
      padding: 20px 15px 0px 15px;
      @include media-xs-min {
        padding: 30px 30px 25px 30px;
      }
    }
    &__body {
      &-capcha {
        margin: 25px 0;
        height: 70px;
      }
      &-chekbox {
        margin-bottom: 25px;
      }
    }
    &__footer {
      padding-top: 0;
      padding-bottom: 20px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      & > * {
        &:not(:last-child) {
          margin-bottom: 25px;
        }
      }
      &-btn {
        width: 100%;
        @include media-xs-min {
          width: fit-content;
        }
      }
    }
  }
  .phones-wrapper {
    @include media-xs-min {
      display: flex;
